"use client"

import { useState } from "react"
import { Container } from "@/components/container"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Calendar, User } from "lucide-react"
import Image from "next/image"
import { getAssetPath } from "@/lib/get-base-path"
import { useLanguage } from "@/lib/i18n-context"


import { useCMS } from "@/lib/cms-context"
import { NewsShareButton } from "@/components/news-share-button"

export default function ArticlesPage() {
    const { t } = useLanguage()
    const { content } = useCMS()

    const categories = [
        t("articles.categories.0") || "All",
        t("articles.categories.1") || "Education",
        t("articles.categories.2") || "Parenting",
        t("articles.categories.3") || "Student Life",
        t("articles.categories.4") || "Health"
    ]

    const [selectedCategory, setSelectedCategory] = useState(categories[0])

    const articlesData = content.articles || []

    // Filter logic
    // Using simple category match for now. In real app, might want to normalize keys.
    const filteredArticles = selectedCategory === (t("articles.categories.0") || "All")
        ? articlesData
        : articlesData.filter(article => {
            // Try to match category vaguely since user enters text
            // Or if article has a specific category field
            return (article as any).category === selectedCategory ||
                (article as any).category?.toLowerCase() === selectedCategory.toLowerCase()
        })


    return (
        <div className="pt-20">
            {/* Hero Section */}
            <section className="relative py-20 text-white overflow-hidden bg-[#2C4F5E]">
                {/* Background Image */}
                <Image
                    src={getAssetPath("/images/news-hero-bg.jpg")}
                    alt="Articles Hero Background"
                    fill
                    className="object-cover opacity-40 mix-blend-overlay"
                    priority
                />

                {/* Abstract Background Elements */}
                <div className="absolute top-0 left-0 w-full h-full overflow-hidden opacity-10">
                    <div className="absolute -top-[20%] -left-[10%] w-[50%] h-[50%] rounded-full bg-white blur-3xl" />
                    <div className="absolute top-[40%] right-[10%] w-[30%] h-[30%] rounded-full bg-[#F5A623] blur-3xl" />
                </div>

                <Container className="relative z-10">
                    <div className="max-w-3xl mx-auto text-center space-y-6">
                        <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold">{t("articles.hero.title")}</h1>
                        <p className="text-xl text-white/90 leading-relaxed">
                            {t("articles.hero.description")}
                        </p>
                    </div>
                </Container>
            </section>

            {/* Articles Section */}
            <section className="py-20 bg-gray-50">
                <Container>
                    {/* Category Filter */}
                    <div className="flex flex-wrap items-center justify-center gap-3 mb-12">
                        {categories.map((category) => (
                            <Button
                                key={category}
                                onClick={() => setSelectedCategory(category)}
                                variant={selectedCategory === category ? "default" : "outline"}
                                className={
                                    selectedCategory === category
                                        ? "bg-[#F5A623] hover:bg-[#FFB84D] text-white"
                                        : "border text-[#2C4F5E] hover:bg-[#2C4F5E] hover:text-white bg-white"
                                }
                            >
                                {category}
                            </Button>
                        ))}
                    </div>

                    {/* Articles Grid - 2 Columns */}
                    <div className="grid md:grid-cols-2 gap-8 items-start">
                        {filteredArticles.map((article) => (
                            <Card key={article.id} className="overflow-hidden bg-white border border-border/40 shadow-sm hover:shadow-md transition-shadow duration-300 h-full flex flex-col">
                                {/* Article Image - Full Width Banner if available */}
                                {article.image && (
                                    <div className="relative h-64 w-full overflow-hidden bg-gray-100 shrink-0">
                                        <Image
                                            src={article.image}
                                            alt={article.title}
                                            fill
                                            className="object-cover"
                                        />
                                        <Badge className="absolute top-4 left-4 bg-[#F5A623] hover:bg-[#FFB84D] text-white border-0 z-10 px-3 py-1 text-sm font-medium shadow-sm">
                                            {article.category}
                                        </Badge>
                                    </div>
                                )}

                                <CardHeader className="p-6 pb-4">
                                    {/* Category if no image (fallback) */}
                                    {!article.image && (
                                        <div className="mb-4">
                                            <Badge className="bg-[#F5A623] hover:bg-[#FFB84D] text-white border-0 px-3 py-1 text-sm font-medium">
                                                {article.category}
                                            </Badge>
                                        </div>
                                    )}

                                    <h2 className="text-2xl font-bold text-[#2C4F5E] mb-4 leading-tight">
                                        {article.title}
                                    </h2>

                                    <div className="flex items-center gap-4 text-sm text-muted-foreground border-b border-border/50 pb-4">
                                        <div className="flex items-center gap-2">
                                            <Calendar className="w-4 h-4" />
                                            <span>{article.date}</span>
                                        </div>
                                        <div className="flex items-center gap-2">
                                            <User className="w-4 h-4" />
                                            <span>{article.author}</span>
                                        </div>
                                    </div>
                                </CardHeader>

                                <CardContent className="p-6 pt-4 flex-grow">
                                    {/* Excerpt */}
                                    <p className="text-base text-foreground/80 leading-relaxed font-medium mb-6">
                                        {article.excerpt}
                                    </p>

                                    {/* Main Content - Prose */}
                                    <div className="prose prose-sm max-w-none text-foreground prose-headings:text-[#2C4F5E] prose-a:text-[#F5A623] prose-img:rounded-xl prose-strong:text-[#2C4F5E]">
                                        <div className="whitespace-pre-wrap">
                                            {article.content}
                                        </div>
                                    </div>

                                    <div className="mt-8 pt-6 border-t border-border/50 flex justify-end">
                                        <NewsShareButton
                                            title={article.title}
                                            url={typeof window !== 'undefined' ? `${window.location.origin}/articles/${article.id}` : undefined}
                                        />
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>

                    {filteredArticles.length === 0 && (
                        <div className="text-center py-20">
                            <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-gray-100 mb-4">
                                <Calendar className="w-8 h-8 text-gray-400" />
                            </div>
                            <p className="text-muted-foreground text-lg">{t("articles.noArticles")}</p>
                        </div>
                    )}
                </Container>
            </section>
        </div>
    )
}
