"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover"
import { Share2, Facebook, Twitter, Phone, MessageCircle } from "lucide-react"
import { useLanguage } from "@/lib/i18n-context"
// Note: lucide-react might not have a dedicated WhatsApp icon, using MessageCircle or Phone as fallback if needed, 
// but often specific brand icons are preferred. For now standard lucide icons. 
// Actually, standard Lucide doesn't have brand icons like Whatsapp. 
// I will use some generic icons or text, or SVG path if I want to be precise, 
// but to be safe and consistent with the codebase I'll use available Lucide icons or simple text labels. 
// Wait, the user asked for "facebook whatsapp x".
// Let's use generic "Share" icon for the button.
// For the options:
// Facebook -> Facebook icon (lucide has it)
// X -> Twitter icon (lucide has it)
// WhatsApp -> MessageCircle (lucide has it) or just text.

export function NewsShareButton({ title, url }: { title: string; url?: string }) {
    const { t } = useLanguage()
    const [shareCount, setShareCount] = useState(0)
    const [isOpen, setIsOpen] = useState(false)
    const [shareUrl, setShareUrl] = useState("")

    useEffect(() => {
        // Set URL on client side to avoid hydration mismatch if using window
        setShareUrl(url || (typeof window !== 'undefined' ? window.location.href : ''))
        console.log("NewsShareButton Props:", { title, url });
    }, [url, title])

    const handleShare = (platform: 'facebook' | 'whatsapp' | 'x') => {
        let link = ''
        const text = encodeURIComponent(title)
        const u = encodeURIComponent(shareUrl)

        switch (platform) {
            case 'facebook':
                link = `https://www.facebook.com/sharer/sharer.php?u=${u}`
                break
            case 'whatsapp':
                link = `https://wa.me/?text=${text}%20${u}`
                break
            case 'x':
                link = `https://twitter.com/intent/tweet?text=${text}&url=${u}`
                break
        }

        window.open(link, '_blank', 'width=600,height=400')
        setIsOpen(false)
        // Optional: increment share count locally to give feedback
        setShareCount(prev => prev + 1)
    }

    return (
        <Popover open={isOpen} onOpenChange={setIsOpen}>
            <PopoverTrigger asChild>
                <Button variant="outline" className="gap-2 text-[#2C4F5E] border-[#2C4F5E] hover:bg-[#2C4F5E] hover:text-white transition-colors">
                    <Share2 className="w-4 h-4" />
                    <span>{t("buttons.share") || "Share"}</span>
                    <span className="bg-[#F5A623] text-white text-[10px] px-1.5 py-0.5 rounded-full ml-1">
                        {shareCount}
                    </span>
                </Button>
            </PopoverTrigger>
            <PopoverContent className="w-auto p-2" align="start">
                <div className="flex gap-2">
                    <Button
                        variant="ghost"
                        size="icon"
                        className="hover:bg-blue-100 hover:text-blue-600 text-[#2C4F5E]"
                        onClick={() => handleShare('facebook')}
                        title="Share on Facebook"
                    >
                        <Facebook className="w-5 h-5" />
                    </Button>
                    <Button
                        variant="ghost"
                        size="icon"
                        className="hover:bg-green-100 hover:text-green-600 text-[#2C4F5E]"
                        onClick={() => handleShare('whatsapp')}
                        title="Share on WhatsApp"
                    >
                        <MessageCircle className="w-5 h-5" />
                    </Button>
                    <Button
                        variant="ghost"
                        size="icon"
                        className="hover:bg-gray-100 hover:text-black text-[#2C4F5E]"
                        onClick={() => handleShare('x')}
                        title="Share on X"
                    >
                        <Twitter className="w-5 h-5" />
                    </Button>
                </div>
            </PopoverContent>
        </Popover>
    )
}
